// 不使用懶加載
import Home from '../components/Home.vue';
import Login from '../components/Login.vue';
import Error404 from '../components/Error404.vue';
import Galleries from '../components/galleries/Galleries.vue';
import About from '../components/About.vue';
import Cart from '../components/carts/Carts.vue';
import History from '../components/History.vue';

export const routes = [{
	path: '',
	// component: Home,
	name: 'home',
	components: {
		default: Home,
	}
}, {
	path: '/index',
	// component: Home,
	name: 'index',
	components: {
		default: Home,
	}
},, {
	path: '/index.html',
	// component: Home,
	name: 'indexHtml',
	components: {
		default: Home,
	}
},{
	path: '/login',
	// component: Home,
	name: 'login',
	components: {
		default: Login,
	}
}, {
	// 輸入非上面的path自動導到某頁
	// 也可以綁component
	path: '/galleries',
	name: 'galleries',
	components: {
		default: Galleries,
	}
}, {
	// 輸入非上面的path自動導到某頁
	// 也可以綁component
	path: '/about',
	name: 'about',
	components: {
		default: About,
	}
}, {
	// 輸入非上面的path自動導到某頁
	// 也可以綁component
	path: '/cart',
	name: 'cart',
	components: {
		default: Cart,
	}
}, {
	// 輸入非上面的path自動導到某頁
	// 也可以綁component
	path: '/history',
	name: 'history',
	components: {
		default: History,
	}
}, {
	// 輸入非上面的path自動導到某頁
	// 也可以綁component
	path: '/error404',
	name: 'error404',
	components: {
		default: Error404,
	}
}, {
	// 輸入非上面的path自動導到某頁
	// 也可以綁component
	path: '*',
	redirect: 'error404'
}];


