<template>
  <section id="hsitory" class="content-hight">
    <br>
    <br>
    <div class="container-fluid">
      <div class="row">
        <div class="col-xs-12 col-sm-4 col-sm-offset-4">
          <div class="text-center">
            <label for="date" class="text-center">選擇日期</label>
          </div>
          <select id="date" class="form-control" v-model="selectedDate" @change="choseDate">
            <option value="">請選擇</option>
            <option v-for="(obj, index) in listHistory" :selected="selectedDate == obj.date" :value="obj.date">{{obj.date}}</option>
          </select>
        </div>
      </div>
      <br>
  
      <div class="row">
        <div class="col-xs-12 col-sm-10 col-sm-offset-1">
          <h2 class="text-center">明細</h2>
          <div class="panel panel-default">
            <table class="table table-striped table-condensed">
              <thead>
                <tr>
                  <th class="center-th">#</th>
                  <th class="center-th">pic</th>
                  <th class="center-th">title</th>
                  <th class="center-th">size</th>
                  <th class="center-th">price</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in items">
                  <td class="center-th">{{(index + 1)}}</td>
                  <td class="center-th">
                    <img :src="item.url" class="pic">
                  </td>
                  <td class="center-th">{{item.title}}</td>
                  <td class="center-th">{{item.size}}</td>
                  <td class="center-th">{{item.price | currency}}</td>
                </tr>
  
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>.
  
    <div class="container">
      <div class="row">
        <div class="col-xs-12 col-sm-4 col-offset-sm-4">
  
        </div>
      </div>
    </div>
  
  </section>
</template>

<script>
import { mapGetters } from 'vuex';
// 載入firebase js
import firebase from 'firebase'
export default {

  name: 'history',


  data() {
    return {
      listHistory: [],
      selectedDate: '',
      items: [],
    };
  }, methods: {
    choseDate() {
      const vm = this;
      if (vm.selectedDate) {
        vm.listHistory.map((n) => {
          if (n.date == vm.selectedDate) {
            vm.items = n.item;
          }
        })
      } else {
        vm.items = [];
      }
    }
  }, computed: {
    // 取得是否有登入的狀態
    ...mapGetters('login', ['isLogin', 'getUserInfo']),
  }, created() {
    let vm = this;
    const uid = vm.getUserInfo.uid
    let firedb = firebase.database();
    let todoRef = firedb.ref(`shopList/facebook/${uid}`);

    // 原本是todoRef.on，造成狂塞資料，
    todoRef.once('value', function (firelist) {
      // 如果有資料的話
      if (firelist.val()) {
        // 取history的節點，裡面是放購買紀錄
        todoRef = firedb.ref(`shopList/facebook/${uid}/history`);
        todoRef.once('value', function (his) {
          const objHistory = his.val();
          if (objHistory) {
            // 因為firebase都是key-value組合所以要重新拆解
            // 第一層有幾個key就是有幾筆交易
            let listArray = [];
            for (let a in objHistory) {
              listArray.push(objHistory[a]);
            }
            vm.listHistory = listArray;
          }
        });
      }
    });
  }
};
</script>

<style lang="scss" scoped>
@import '../assets/style/scss/history.scss';
</style>


