<template>
    <div class="col-xs-12 col-sm-6 col-md-4">
    
        <div class="thumbnail card-shadow">
    
            <a :href="gallery.url" data-lightbox="roadtrip" :data-title="gallery.title">
    
                <div class="mask-item">
                    <img :src="gallery.url" alt="" class="img-responsive">
                    <div class="overlay">
                        <i class="fa fa-search-plus ab-center " aria-hidden="true"></i>
                    </div>
                </div>
            </a>
            <div class="caption">
                <h2 class="modal-title">{{gallery.title}}</h2>
                <ul>
                    <li v-for="(size, i) in gallery.sizes">
                        <input v-if="size.type === 'M'" type="radio" :name="'size-' + gallery.id" :value="size.type" checked="checked" v-model="orderSize" :disabled="gallery.quantity < 1">
                        <input v-else type="radio" :name="'size-' + gallery.id" :value="size.type" v-model="orderSize" :disabled="gallery.quantity < 1">
                        <div class="detail">
                            <div class="size-title">{{size.title}}</div>
                            <div class="size">{{size.size}}</div>
                        </div>
                        <div class="price">
                            {{size.price | currency}}
                        </div>
                    </li>
                </ul>
                <!-- 判斷是否要disable是看庫存量是否小於1 -->
                <!-- @click="addCart();isBuy = !isBuy" -->
                <button class="btn btn-black" @click="addCart();" :disabled="(gallery.quantity < 1)">{{gallery.quantity
                    <1 ? '已購買' : '加入購物車'}}</button>
            </div>
        </div>
    </div>
</template>
<script>
// 載入store用方法
import { mapActions, mapGetters } from 'vuex'
import { shopBus } from '../../main'

export default {
    // 這個是從Galleries.vue綁定的參數
    props: ['gallery'],
    data() {
        return {
            //透過這個參數去看現在是綁定哪個大小
            orderSize: 'M',
            isBuy: false,
        };
    },
    methods: {
        //按下購買
        addCart() {
            // 如果沒有登入不能購買，彈跳對話視窗告知先登入
            const vm = this;
            if (!vm.isLogin) {
                let obj = {
                    title: '登入',
                    message: '必須登入才可以進行購物',
                    type: 'custom',
                    hideAllButton: true,
                    customIconUrl: 'https://cdn2.iconfinder.com/data/icons/social-productivity-line-art-1/128/face-sad-512.png'
                }
                shopBus.$emit('callAlert', obj)
                return false;
            }
            // 多增加的訊息要存放
            const other = {
                size: vm.orderSize,
                price: vm.orderSize == 'M' ? 34 : 99
            }
            // 合併物件的方法，
            let orderCart = Object.assign(vm.gallery, other);
            // 因為記憶體還是同一個所以重新指定
            orderCart = JSON.parse(JSON.stringify(orderCart));
            // 合併物件的方法
            let orderGallery = Object.assign(vm.gallery, other);
             // 因為記憶體還是同一個所以重新指定
            orderGallery = JSON.parse(JSON.stringify(orderGallery));

            // 呼叫cart這個store去存放購買資料
            vm.setCarts(orderCart);
            // 呼叫gallery的store去將數量減1，用來判斷是否有買過了
            vm.buyGalleries(orderGallery);
        },
        // 注入store的name是cart
        ...mapActions('cart', ['setCarts']),
        ...mapActions('gallery', ['buyGalleries'])
    },
    computed: {
        // 取得是否有登入
        ...mapGetters('login', ['isLogin']),
    },
};
</script>
<style lang="scss" scoped>
@import '../../assets/style/scss/galleries/gallery.scss';
</style>



