<template>
    <tr>
        <td class="center-th">
            {{index + 1}}
        </td>
        <td class="center-th">
            <img :src="cart.url" class="pic">
        </td>
        <td class="center-th">
            {{cart.title}}
        </td>
        <td class="center-th">
        	{{cart.size}}
        </td>	
        <td class="center-th">
            <button class="btn btn-default btn-black" name="button" @click="deletItem(cart)">&times;</button>
        </td>
        
    </tr>
</template>
<script>
// 載入store用方法
import {
    mapActions
} from 'vuex'
export default {
    props: ['cart', 'index'],
    name: 'cart',

    data() {
        return {

        };
    },
    methods: {
        // 注入store的name是cart
        ...mapActions('cart', ['setCarts', 'removeCarts']),
        // 注入store的name是gallery
        ...mapActions('gallery', ['initGalleries', 'reBackGalleries']),
        deletItem(order) {
            const vm = this;
            vm.removeCarts(order);
            vm.reBackGalleries(order);
        }
    }
};
</script>
<style lang="scss" scoped>
@import '../../assets/style/scss/carts/cart.scss';
</style>



