const state = {
	// 存放登入者資訊
	user: null,
	// onAuthStateChanged 會偵測有沒有人登入，如果沒有登入我會先update成false
	isLogin: false,
}

// 宣告全域變數可以取得的方法getter
/**
 * [getter description]
 * @type {Object}
 */
const getters = {
	// 取出
	isLogin: state => {
		return state.isLogin;
	},
	getUserInfo: state => {
		return state.user;
	}
}
const mutations = {
	login: (state, user) => {

		// 有登入的話要將isLogin變true
		state.isLogin = true;
		state.user = user;
	},
	logout: (state) => {
		// 登出清空
		state.isLogin = false;
		state.user = {};
	}
}

// 外面方法是先呼叫actions->mutations
const actions = {
	// 初始化資料
	login: ({
		commit
	}, user) => {
		// 呼叫mutations的方法
		commit('login', user);

	},
	logout: ({
		commit
	}) => {
		commit('logout');
	}
}

export default {
	state,
	getters,
	mutations,
	actions,
	namespaced: true
}


