// 基本要載入Vue、Vuex
import Vue from 'vue'
import Vuex from 'vuex'
// 載入自訂全域變數的module
import gallery from './module/gallery'
import cart from './module/cart'
import login from './module/login'

Vue.use(Vuex);
export const store = new Vuex.Store({
	// 注入我自己寫的模組
	modules: {
		gallery,
		cart,
		login
	}
})

