<template>
    <section id="carts" class="content-hight">
        <div class="container content-hight">
            <!-- <div class="row"> -->
            <!-- <h2 class="text-center">Order List</h2> -->
            <!-- <app-cart v-for="(cart, index) in carts" :cart="cart" :key="cart"></app-cart> -->
            <div class="col-sm-12">
                <h2 class="text-center">Order List </h2>
                <div class="panel panel-default">
                    <table class="table table-striped table-condensed">
                        <thead>
                            <tr>
                                <th class="center-th">#</th>
                                <th class="center-th">Pic</th>
                                <th class="center-th">Title</th>
                                <th class="center-th">Size</th>
                                <th class="center-th">Delete</th>
                            </tr>
                        </thead>
                        <tbody>
                            <!-- 放有購買的清單 -->
                            <app-cart v-for="(cart, index) in carts" :cart="cart" :key="cart" :index="index"></app-cart>
                        </tbody>
                    </table>
                </div>
                <!-- </div> -->
    
                <button v-if="carts.length > 0" class="btn btn-default btn-black" @click="buyPhoto">Buy</button>
            </div>
        </div>
        </div>
    </section>
</template>
<script>
import Cart from './Cart.vue'
// store變數的作法
import {
    mapGetters
} from 'vuex';
import { shopBus } from '../../main';
export default {

    name: 'carts',

    data() {
        return {

        };
    },
    computed: {
        ...mapGetters('cart', ['carts']),
    },
    components: {
        appCart: Cart
    },
    methods: {
        buyPhoto() {
            shopBus.$emit('buyPhot')
        }
    }
};
</script>
<style lang="scss" scoped>
@import '../../assets/style/scss/carts/carts.scss';
</style>



