/**
 *	這個是用vuex開放全域變數的資料設定
 *  分成用來專用gallery模組
 *  會開發共用的變數拿來存取
 */

// 先載入相簿的json資料
import newGalleries from '../../data/galleries'

const state = {
	// 存放目前有的gallery所以資料
	galleries: [],
}

// 宣告全域變數可以取得的方法getter
/**
 * [getter description]
 * @type {Object}
 */
const getters = {
	// 取出
	galleries: state => {
		return state.galleries;
	}
}
const mutations = {
	setGallery: (state) => {
		// 如果複製的資料相同，新的變數會影響原來的變數
		// 所以要先copy一份出來（但這個只有淺層，還是會影響）
		let a = Object.assign([], newGalleries);
		// 所以將它轉成字串再轉回來就可以是一個新的
		let copy = JSON.parse(JSON.stringify(a));

		// 或是可以使用lodash的clonedeep

		// 將資料塞到state.gallery
		state.galleries = copy;
	},
	buyGalleries: (state, order) => {
		// 將按下購買的商品的數量-1
		// 用map找出購買相同id的商品
		state.galleries.map((inventor) => {
			if (inventor.id == order.id) {
				if (inventor.quantity > 0) {
					inventor.quantity -= 1
				}
			}
		});
	},
	reBackGalleries: (state, order) => {
		state.galleries.map((inventor) => {
			if (inventor.id == order.id) {

				if (inventor.quantity == 0) {
					inventor.quantity += 1
				}

			}
		});
	}
}
const actions = {
	// 初始化資料
	initGalleries: ({
		commit
	}) => {
		commit('setGallery');
	},
	buyGalleries: ({
		commit
	}, order) => {
		commit('buyGalleries', order);
	},
	reBackGalleries: ({
		commit
	}, order) => {
		commit('reBackGalleries', order);
	},
}

export default {
	state,
	getters,
	mutations,
	actions,
	namespaced: true
}


