<template>
    <section id="galleries" class="content-hight">
        <!-- 商品清單 -->
        <div class="container">
            <div class="row">
                <!-- 因為我後面要依照選擇到哪個大小去加入購物車 如果不再拆一個component出來會不好寫 -->
                <!-- 利用v-bind:gallery 去傳遞資料 -->
                <!-- 如果接收參數的componet有用slot必須加:key綁定 -->
                <app-gallery v-for="(gallery, index) in galleries" :gallery="gallery" :key="gallery"></app-gallery>
            </div>
        </div>
    </section>
</template>
<script>
import Gallery from './Gallery.vue'
// store變數的作法
import {
    mapGetters
} from 'vuex';
export default {

    name: 'galleries',
    data() {
            return {
                
            };
        },
        methods: {
        },
        components: {
            appGallery: Gallery
        },
        computed: {
            // 比較不好的寫法直接取設定全域變數內的值
            // this.$store.getters.getGalleries


            // 使用此語法必須安裝npm i --save-dev babel-preset-stage-2
            // 裡面的key是要呼叫的方法名稱
            // 裡面也可以包物件在另外取名稱
            ...mapGetters('gallery',['galleries']),
        },
};
</script>
<style lang="scss" scoped>
@import '../../assets/style/scss/galleries/galleries.scss';
</style>



