<template>
    <div id="app">
        <!-- header共用 -->
        <app-header></app-header>
        <transition name="fade" mode="out-in">
            <!-- 使用keep-alive來保持輸入過的元素 -->
            <!-- exclude是用來排除妳不要keep的component 裡面就寫在.vue設定的name屬性 -->
            <!--<keep-alive include="login galleries">-->
            <keep-alive include="login">
                <router-view></router-view>
            </keep-alive>
        </transition>
        <!-- footer共用 -->
        <app-footer></app-footer>
        <span></span>
        <!--alert視窗共用-->
        <!--  <simplert useRadius=true useIcon=true ref="simplert">
                                                    </simplert> -->
        <!-- 有兩個版本要測試看哪個正常 -->
        <simplert isUseRadius=true isUseIcon=true ref="simplert">
        </simplert>
    </div>
</template>
<script>
import Header from './components/Header.vue'
import Footer from './components/Footer.vue'
// alert plugIn
import Simplert from 'vue2-simplert'
// import main.js宣告的bus
import {
    shopBus
} from './main'
import {
    mapActions,
    mapGetters
} from 'vuex';

// 載入firebase js
import firebase from 'firebase'

// 載入firebase data 之後要用在拿來使用
import firebaseData from '../src/firebase/firebaseData';
// const fireApp = firebase.initializeApp(firebaseData);


export default {
    name: 'app',
    fireApp: null,
    data() {
        return {
            msg: 'Welcome to Your Vue.js App'
        }
    },
    created() {
        const vm = this;
        // 初始化，宣告成一個變數是之後可以拿來用
        firebase.initializeApp(firebaseData);


        // 進行初始化

        // vue create時候先初始化
        // 切換頁面不會重新create，但若是使用url做切換或是refresh就又會重新長一次
        // this.$store.dispatch('initGalleries');
        // 改用mapActions
        vm.initGalleries();

        // 監聽其他頁面傳來的alert訊號
        shopBus.$on('callAlert', (obj) => {
            vm.callAlert(obj);
        });

        // 偵測呼叫loginFb
        shopBus.$on('loginShop', (obj) => {
            vm.loginShop(obj);
        });
        shopBus.$on('logoutShop', (obj) => {
            vm.logoutShop(obj);
        });
        shopBus.$on('buyPhot', (obj) => {
            vm.buyPhoto(obj);
        });

    },
    components: {
        appHeader: Header,
        appFooter: Footer,
        Simplert

    },
    methods: {
        // 登出
        logoutShop(obj) {
            if (obj == 'fb') {
                firebase.auth().signOut().then(function () {
                    // Sign-out successful.
                    console.log('fb登出');
                    // 因為這裡不能再呼叫其他方法所以都改在onAuthStateChanged去判斷
                }, function (error) {
                    // An error happened.
                    console.log('fb登出失敗');
                });
            }
        },
        // 登入
        loginShop(obj) {
            // 如果登入的是fb
            if (obj == 'fb') {

                var provider = new firebase.auth.FacebookAuthProvider();

                provider.setCustomParameters({
                    'display': 'popup'
                });
                provider.addScope('email');
                provider.addScope('public_profile');
                // provider.addScope('user_birthday');
                firebase.auth().signInWithPopup(provider).then(function (result) {
                    // This gives you a Facebook Access Token. You can use it to access the Facebook API.
                    var token = result.credential.accessToken;
                    // The signed-in user info.
                    // console.log("token:" + user);
                    var user = result.user;
                }).catch(function (error) {
                    // Handle Errors here.
                    const errorCode = error.code;
                    const errorMessage = error.message;
                    console.log("errorCode:" + errorCode);
                    console.log("errorMessage:" + errorMessage);
                    // The email of the user's account used.
                    const email = error.email;
                    console.log("email:" + email);
                    // The firebase.auth.AuthCredential type that was used.
                    const credential = error.credential;
                    console.log("credential:" + credential);
                });
                // console.log(firebase.auth().currentUser);
            } else if (obj == 'email') {

            }

        },
        // 因為要使用alert方法開在最外層讓其他子視窗呼叫(使用bus做法)
        // obj是alert的參數
        callAlert(obj) {
            const vm = this;
            vm.$refs.simplert.openSimplert(obj)
        },
        buyPhoto(obj) {
            const vm = this;
            // DB連線
            let firedb = firebase.database();

            // 塞到firebase前置動作
            // step1.先判斷這個使用者有沒有買過的紀錄存放在firebase，所以要先去撈資料
            // 取出目前登入者的資訊，去比對資料
            const userInfo = vm.getUserInfo;
            let todoRef = firedb.ref('shopList/facebook/' + userInfo.uid);

            // 原本是todoRef.on，造成狂塞資料，
            todoRef.once('value', function (firelist) {
                
                // console.log(firelist.val());
                // app.notes = firelist.val();
                if (firelist.val()) {
                    // 有購買過
                    // 只要新增購買紀錄，就不用再新增使用者資料                    
                    // 本次購買紀錄
                    todoRef = firedb.ref('shopList/facebook/' + userInfo.uid + '/history');

                    let list = {};
                    list.item = vm.carts;
                    // 增加紀錄購買時間
                    list.date = new Date(Date.now()).toLocaleString();
                   
                    // 回塞key
                    let key = todoRef.push(list).key;
                    todoRef.child(key).update({
                        key: key
                    });


                } else {
                    // 沒有購買過
                    // 要先新增使用者資訊
                    todoRef = firedb.ref('shopList/facebook/' + userInfo.uid + '/userInfo');
                    let key = todoRef.push(userInfo).key;
                    todoRef.child(key).update({
                        key: key
                    });

                    // 本次購買紀錄
                    todoRef = firedb.ref('shopList/facebook/' + userInfo.uid + '/history');
                    
                    let list = {};
                    list.item = vm.carts;
                    // 增加紀錄購買時間
                    list.date = new Date(Date.now()).toLocaleString();

                    // 回塞key
                    key = todoRef.push(list).key;
                    todoRef.child(key).update({
                        key: key
                    });
                }

                // 新增完畢後都清空購買紀錄
                // Galleries復原
                vm.initGalleries();

                // 清掉購買未結帳的清單
                vm.clearCarts();


                // 都好了就彈跳訊息說購買成功
                let obj = {
                    isShown: true,
                    customCloseBtnText: 'OK',
                    title: '購買成功',
                    message: '按下確認轉到明細頁面',
                    type: 'success',
                    onClose: vm.shopScuess
                    // customIconUrl: 'https://cdn2.iconfinder.com/data/icons/social-productivity-line-art-1/128/face-sad-512.png'
                }
                vm.callAlert(obj)
            });
        },
        shopScuess() {
            // 購買成功後導去購買明細
            const vm = this;
            vm.$router.push({
                name: 'history'
            });
        },
        //指定哪一個模組的方法
        ...mapActions('gallery', ['initGalleries']),
        ...mapActions('login', ['login', 'logout']),
        ...mapActions('cart', ['clearCarts']),

    },
    computed: {
        // 取得是否有登入的狀態
        ...mapGetters('login', ['isLogin', 'getUserInfo']),
        ...mapGetters('cart', ['carts']),
    },
    mounted() {
        // 偵測現在是不是有使用者登入或登出
        firebase.auth().onAuthStateChanged(user => {
            const vm = this;
            
            // 偵測是否有使用者資料且vue沒有紀錄登入(isLogin)
            if (user && !vm.isLogin) {
                // 要登入的時候isLogin 是 false狀態
                console.log('＠＠＠＠登入狀態＠＠＠＠');
                const currentUser = firebase.auth().currentUser;
                
                const userIfno = {};
                // 取出要用的資料
                userIfno.name = currentUser.displayName;
                userIfno.email = currentUser.email;
                userIfno.photoUrl = currentUser.photoURL;
                userIfno.uid = currentUser.uid;

                // 將user資料塞到全域變數
                vm.login(userIfno);
                // 再導到首頁
                vm.$router.replace({
                    name: 'home'
                });
            } else if (vm.isLogin) {
                // 原本是還有keep isLogin還沒清是true
                console.log('＠＠＠＠登出狀態＠＠＠＠')
                // 登出就要清掉
                vm.logout();
                // replace作法是不會佔用蘭覽器的history紀錄
                // this.$router.replace({
                //     name: 'home'
                // });

                // Galleries復原
                vm.initGalleries();

                // 清掉購買未結帳的清單
                vm.clearCarts();

                // 再導到首頁
                vm.$router.replace({
                    name: 'home'
                });
            } else {
                //程式剛載入點 
                console.log('＠＠＠＠未登入狀態＠＠＠＠');
                // 以防萬一還是先清空user資訊
                vm.logout();
            }
        });
    }
}
</script>
<style lang="scss">
@import './assets/style/scss/app.scss';
@import './assets/style/scss/grid.scss';
</style>



