                                </div>
                            </div>
                        </div>
                        <div class="item">
                            <div class="img" :style="{ 'background-image': 'url(https://i.imgur.com/qxeOFMf.jpg)' }"></div>
                            <div class="container" >
                                <div class="carousel-caption">
                                    <h3>Portrait</h3>
                                    <p>Welcome!</p>
                                    <!-- <p><a class="btn btn-lg btn-primary" href="#" role="button">Sign up today</a></p> -->
                                </div>
                            </div>
                        </div>
                        <div class="item">
                            <div class="img" :style="{ 'background-image': 'url(https://i.imgur.com/kItGrHz.jpg)' }"></div>
                            <div class="container">
