</template>
<script>
export default {
    name: 'home',
    data() {
        return {

        };
    },
    // 當要離開目前的router觸發
    // 之後用這個來判斷登入者
    beforeRouteLeave(to, from, next) {
        console.log("beforeRouteLeave");
        if (true) {
            next()
        } else {
            next(false)
        }
    },
    beforeRouteEnter(to, from, next) {
        // 應該是可以寫邏輯判斷說是否要進來這一頁
        console.log("beforeRouterEnter")
        if (true) {
            next();
        } else {
            next(false);
        }
    }
};
</script>
<style lang="scss" scoped>
@import '../assets/style/scss/home.scss';
</style>



