<template>
    <!-- <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeIn" mode="in-out"> -->
        <section id="error404">
            <div class="jumbotron content-hight">
                <div class="container text-center">
                    <div class="ab-center">

                        <h2>Ooops!</h2>
                        <br>
                        <!-- 使用vue-count-to的plung in -->
                        <!-- render畫面的時候會產生一個span -->
                        <!-- ref屬性就是span的id class是賦與他的class屬性 -->
                        <countTo :startVal='startVal' :endVal='endVal' :duration='2300' ref='error404' class='error404'>
                        </countTo>
                        <br>
                        <button @click="goBack" class="btn btn-default">Go Home</button>
                    </div>
                </div>
            </div>
        </section>
    <!-- </transition> -->
</template>
<script>
// 數字亂數plungin
import countTo from 'vue-count-to';
export default {
    name:'error404',
    // 需要掛載到components
    components: {
        countTo
    },
    data() {
        return {
            startVal: 0,
            endVal: 404
        }
    },
    methods: {
        goBack() {
            const vm = this
            // 瀏覽器回上一頁的作法
            // 參數 加減號代表要往前還往後幾個
            // vm.$router.go(-1);
            // 還是直接回首頁好，用replace把錯誤訊息的history蓋掉
            vm.$router.replace({
                name: 'home'
            });
        }
    }
};
</script>
<style lang="scss" scoped>
@import '../assets/style/scss/error/error404.scss';
</style>



