<template>
    <section id="login" class="content-hight">
        <!-- 因為container高度抓不到所以我自己把它撐開 -->
        <div class="container content-hight">
        
            <div class="form-group card-shadow ab-center">
                <!-- 其他登入的方法有空再做 -->
                <!-- <label for="aa" class="email">Email</label>
                <input type="text" name="email" id="email" placeholder="Email" class="form-control">
                <label for="aa" class="passWord">Password</label>
                <input type="text" name="passWord" id="passWord" placeholder="Password" class="form-control">
                <button @click="loginEmail" class="btn btn-default btn-black">Log In</button> -->
                <button @click="loginWithFb" class="btn btn-default btn-black facebook">Connect With FaceBook</button>
            </div>
        </div>
    </section>
</template>
<script>
import {
    mapGetters
} from 'vuex'
import {
    shopBus
} from '../main'
export default {
    name: 'login',
    data() {
        return {
            title: 'Login'
        };
    },created(){
       
    },
    methods: {
        loginEmail() {
            // 呼叫在App.vue設定的方法
            // 先隨便帶參數而已（沒有用處）
            // shopBus.$emit('loginFb', 1)
        },
        loginWithFb() {
            shopBus.$emit('loginShop', 'fb')

        }
    },
    computed: {
        // 取得是否有登入
        ...mapGetters('login', ['isLogin']),
    },
    beforeRouteLeave(to, from, next) {
        console.log("beforeRouteLeave");
        if (true) {
            next()
        } else {
            next(false)
        }
    },
    beforeRouteEnter(to, from, next) {
        // 應該是可以寫邏輯判斷說是否要進來這一頁

        if (true) {
            // 在routerEnter無法使用this，所以要改下面這樣寫法vm，才可以抓到
            next(vm => {
                // console.log("@@@@@" + vm.isLogin)
                // console.log("#####" + vm.$route.path)
                if (vm.isLogin) {
                    // 以防萬一有人登入了又再一次進來就把它導回去前一頁
                    vm.$router.go(-1);
                    // vm.$router.replace({
                    //     name: 'home'
                    // });
                }
            })
        } else {
            next(false);
        }
    },
    beforeRouteUpdate(to, from, next) {
        console.log("beforeRouteUpdate")
        if (true) {
            next();
        } else {
            next(false);
        }
    },

};
</script>
<style lang="scss" scoped>
@import '../assets/style/scss/login.scss';
</style>



