//用來看買了哪些品


const state = {
	// 存放目前已購買的清單
	carts: [],
}

// 宣告全域變數可以取得的方法getter
/**
 * [getter description]
 * @type {Object}
 */
const getters = {
	// 取出
	carts: state => {
		return state.carts;
	}
}
const mutations = {
	setCarts: (state, carts) => {
		// 比對現在存在carts內是否有同樣的資料
		const record = state.carts.find(element => element.id == carts.id);
		// 如果原本沒有相同購買的資料才要再塞
		if (!record) {
			state.carts.push(carts);
		}
	},
	removeCarts: (state, carts) => {
		state.carts.map((inventor, index) => {
			if (inventor.id == carts.id) {
				state.carts.splice(index, 1);
			}
		});
	},

	clearCarts: (state) => {
		// 登出後清空購買
		state.carts = [];
	}
}

// 外面方法是先呼叫actions->mutations
const actions = {
	// 初始化資料
	setCarts: ({
		commit
	}, carts) => {
		// 呼叫mutations的方法
		commit('setCarts', carts);
	},
	removeCarts: ({
		commit
	}, carts) => {
		commit('removeCarts', carts);
	},
	clearCarts: ({
		commit
	}) => {
		commit('clearCarts');
	}

}

export default {
	state,
	getters,
	mutations,
	actions,
	namespaced: true
}


