<template>
    <header id="header">
        <div class="container">
            <nav class="navbar navbar-default navbar-inverse nav-setting navbar-fixed-top" role="navigation">
                <div class="container-fluid">
                    <div class="navbar-header ">
                        <button type="button" class="navbar-toggle" data-toggle="collapse" data-target=".navbar-ex1-collapse">
                            <span class="sr-only">選單切換</span>
                            <span class="icon-bar"></span>
                            <span class="icon-bar"></span>
                            <span class="icon-bar"></span>
                        </button>
                        <!-- 如果是要綁定物件要加v-bind -->
                        <router-link class="navbar-brand logo" :to="{name: 'home'}">CHU.BSH</router-link>
                        <!-- 也可以直接填寫url -->
                        <!-- <router-link class="navbar-brand" to="/">秘密購物</router-link> -->
                    </div>
                    <div class="collapse navbar-collapse navbar-ex1-collapse">
                        <!-- 右選單 -->
                        <ul class="nav navbar-nav navbar-right fix-fix-top">
                            <li>
                                <router-link class="navbar-brand " to="/dssdf"><i class="fa fa-exclamation-triangle" aria-hidden="true"></i> HTTP 404</router-link>
                            </li>
                            <li>
                                <router-link class="navbar-brand " :to="{name: 'galleries'}"><i class="fa fa-camera-retro" aria-hidden="true"></i> Galleries</router-link>
                            </li>
                            <li v-if="isLogin">
                                <router-link class="navbar-brand " :to="{name: 'history'}"><i class="fa fa-id-card-o" aria-hidden="true"></i> History</router-link>
                            </li>
                            <li v-if='isLogin'>
                                <router-link class="navbar-brand " :to="{name: 'cart'}"><i class="fa fa-shopping-cart" aria-hidden="true">
                                    <span v-show="carts.length > 0" class="cartNum">{{carts.length}}</span>
                                </i> Cart</router-link>
                            </li>
                            <li v-if='isLogin'>
                                <!--使用prevent讓他不跳轉-->
                                <a href="" class="navbar-brand " @click.prevent="logout"><i class="fa fa-sign-out" aria-hidden="true"></i> Log Out</a>
                            </li>
                            <li v-else>
                                <router-link class="navbar-brand " :to="{name: 'login'}"><i class="fa fa-sign-in" aria-hidden="true"></i> Log In</router-link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </div>
    </header>
</template>
<script>
import {
    shopBus
} from '../main'
import {
    mapGetters
} from 'vuex';
export default {
    data() {
        return {
            // 暫時設定的(偵測是否有使用者登入)
            signToogle: false
        }
    }, methods: {
        logout() {
            shopBus.$emit('logoutShop', 'fb')

        },
    }, computed: {
        // 取得是否有登入的狀態
        ...mapGetters('login', ['isLogin']),
        // 看有沒有購物有的話給他小圖示
        ...mapGetters('cart', ['carts']),
    },
};
</script>
<style lang="scss" scoped>
@import '../assets/style/scss/header.scss';
</style>



